* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  max-width: 400px;
  background-color: #fff;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

#datetime-picker {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
}

[data-start], [data-stop] {
  min-width: 100%;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-top: 5px;
  margin-bottom: 5px;
  padding: 10px;
  transition: background-color .3s;
  display: block;
}

[data-start]:disabled, [data-stop]:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

[data-start]:hover, [data-stop]:hover {
  background-color: #0056b3;
}

.timer {
  text-align: center;
  margin-top: 20px;
}

.field {
  margin: 0 10px;
  display: inline-block;
}

.field .value {
  font-size: 20px;
  font-weight: bold;
}

.field .label {
  color: #888;
  font-size: 14px;
  display: block;
}

.form {
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

label {
  margin-bottom: 10px;
  font-weight: bold;
  display: block;
}

input[type="number"] {
  width: 100px;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 5px 10px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 3px;
  padding: 8px 15px;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}

